
.content.shop {
  line-height: 48px;
  background-color: #000;
  background-color: rgba(0, 0, 0, 0.4);
  color: #fff;
  position: absolute;
  height: 48px;
}
.content.shop .placeheader {
    line-height: 48px;
    height: 48px;
}
.content {
  background-color: #1e89e0;
  width: 100%;
}
.content .placeheader {
    width: 1180px;
    margin: 0 auto;
    line-height: 60px;
    height: 60px;
}
.content .placeheader .left {
      float: left;
}
.content .placeheader .left h1 {
        line-height: inherit;
        display: inline-block;
        margin-right: 30px;
}
.content .placeheader .left h1 .icon-logo {
          float: left;
          line-height: inherit;
}
.content .placeheader .left h1 .icon-logo:before {
            color: #fff;
            font-size: 32px;
            vertical-align: top;
}
.content .placeheader .left h1 .icon-logo span {
            display: block;
            height: 0;
            overflow: hidden;
            text-indent: -900em;
}
.content .placeheader .left ul {
        display: inline-block;
        line-height: inherit;
}
.content .placeheader .left ul li {
          float: left;
          width: 112px;
          text-align: center;
}
.content .placeheader .left ul li a {
            color: white;
            display: block;
            height: 100%;
            width: 100%;
}
.content .placeheader .left ul li a.focus {
              background-color: #006bc7;
}
.content .placeheader .right {
      float: right;
      line-height: inherit;
      width: 500px;
}
.content .placeheader .right ul {
        float: left;
}
.content .placeheader .right ul li {
          float: left;
}
.content .placeheader .right ul li a {
            color: #d2e7f9;
            width: 106px;
            line-height: inherit;
            display: block;
            padding: 0 10px;
            text-align: center;
            font-size: 14px;
}
.content .placeheader .right ul li a:before {
              padding-right: 5px;
              vertical-align: middle;
              font-size: 22px;
}
.content .placeheader .right ul li a:hover {
              color: white;
}
.content .placeheader .right .login {
        float: right;
}
.content .placeheader .right .login .icon-profile {
          color: white;
}
.content .placeheader .right .login .icon-profile:before {
            padding-right: 10px;
            color: white;
            font-size: 22px;
            vertical-align: middle;
}



