
.search {
  position: relative;
  height: 103px;
}
.search .location {
    position: absolute;
    top: 0;
    left: 0;
}
.search .location .location-current a {
      color: #333;
}
.search .location .location-current a:hover {
        color: #0089dc;
}
.search .place-search {
    position: absolute;
    top: 15px;
    right: 0;
    height: 32px;
    border: 1px solid #ccc;
    background: #fff;
    padding-right: 10px;
}
.search .place-search a {
      font-size: 20px;
}
.search .place-search input {
      width: 236px;
      border: 0;
      outline: 0;
      transition: width .3s ease-in-out;
      padding: 0 8px;
      margin: 4px 0;
      font-size: 14px;
      line-height: 24px;
}
.search .place-tab img {
    width: 186px;
    height: 55px;
    vertical-align: middle;
    position: absolute;
    top: 57px;
    right: 0;
    z-index: 10;
}



