
.shop .shop-main {
  padding-top: 48px;
  background: url(./shop-bg.0391dd.jpg);
}
.shop .shop-main .container {
    width: 1180px;
    height: 142px;
    display: table;
    box-sizing: border-box;
    position: relative;
}
.shop .shop-main .container .shop-info {
      display: table-cell;
      vertical-align: middle;
      color: #FFFFFF;
}
.shop .shop-main .container .shop-info img {
        margin-right: 15px;
        width: 95px;
        height: 95px;
        border-radius: 50%;
        vertical-align: middle;
}
.shop .shop-main .container .shop-info .shop-info-wrapper {
        display: inline-block;
        vertical-align: middle;
}
.shop .shop-main .container .shop-info .shop-info-wrapper h1 {
          font-size: 20px;
          max-width: 270px;
}
.shop .shop-main .container .shop-info .shop-info-wrapper .shopguide-info-rate {
          margin: 10px 0;
}
.shop .shop-main .container .shopguide-server {
      width: 700px;
      display: table-cell;
      padding-right: 100px;
      vertical-align: middle;
      color: #fff;
      text-align: right;
}
.shop .shop-main .container .shopguide-server span {
        display: inline-block;
        vertical-align: top;
        text-align: center;
        margin-left: 80px;
}
.shop .shop-main .container .shopguide-server span .shopguide-server-value {
          display: block;
          margin-top: 12px;
          margin-bottom: 3px;
          font-size: 18px;
}
.shop .shop-main .container .shopguide-favor {
      position: absolute;
      top: 0;
      right: 0;
      padding: 10px 5px;
      width: 80px;
      text-align: center;
      background: #FFF;
      background: rgba(255, 255, 255, 0.1);
      border-radius: 0 0 5px 5px;
      color: #eee;
      text-align: center;
}
.shop .shop-main .container .shopguide-favor i {
        font-size: 20px;
}
.shop .shop-main .container .shopguide-favor span {
        display: block;
}



