
.home {
  width: 100%;
  height: 500px;
}
.home .main {
    height: 500px;
    background: url("map.a76df5.png");
    position: relative;
}
.home .main .content {
      width: 1180px;
      margin: 0 auto;
      height: 100%;
      box-sizing: border-box;
}
.home .main .content .header {
        padding-top: 25px;
}
.home .main .content .header h1 {
          float: left;
          width: 220px;
          height: 49px;
}
.home .main .content .header h1 a {
            display: block;
}
.home .main .content .header h1 a img {
              width: 220px;
}
.home .main .content .header .home-right {
          float: right;
          word-spacing: 10px;
}
.home .main .content .header .home-right a {
            color: white;
            font-size: 14px;
            display: inline-block;
}
.home .main .content .header .home-right span {
            color: white;
            display: inline-block;
}
.home .main .content .header .home-right .store {
            padding: 7px 14px;
            background-color: #5db423;
}
.home .main .content .container {
        margin-top: 95px;
        height: 380px;
        position: relative;
}
.home .main .content .container:after {
          content: '';
          position: absolute;
          bottom: 0;
          left: 0;
          width: 447px;
          height: 502px;
          background: url("image_kobe.png");
          -webkit-transform: scale(0.7);
                  transform: scale(0.7);
          -webkit-transform-origin: left bottom;
                  transform-origin: left bottom;
}
.home .main .content .container h2 {
          text-align: center;
          margin-bottom: 53px;
}
.home .main .content .container .search {
          width: 50%;
          height: 56px;
          margin: 0 auto;
}
.home .main .content .container .search .city {
            float: left;
            width: 100px;
            position: relative;
}
.home .main .content .container .search .city .current {
              display: block;
              position: relative;
              background-color: #fff;
              text-align: center;
              height: 56px;
              font-size: 20px;
              color: #333;
              line-height: 56px;
              overflow: hidden;
}
.home .main .content .container .search .city .current:after {
                position: absolute;
                content: '';
                top: 45%;
                right: 13px;
                margin-left: 5px;
                vertical-align: middle;
                border-left: 5px solid transparent;
                border-right: 5px solid transparent;
                border-top: 5px solid #999;
}
.home .main .content .container .search .city .city-content {
              position: absolute;
              z-index: 1200;
              top: 76px;
              background-color: #fff;
              width: 730px;
              box-shadow: 0 20px 20px 0 rgba(0, 0, 0, 0.085);
              padding: 0 30px 0 23px;
              overflow-y: auto;
              max-height: 300px;
}
.home .main .content .container .search .city .city-content:before {
                content: '';
                position: absolute;
                top: 0;
                left: 50px;
                margin-top: -20px;
                border: 10px solid transparent;
                border-bottom-color: #fff;
}
.home .main .content .container .search .city .city-content .city-header {
                border-bottom: 1px solid #eee;
}
.home .main .content .container .search .city .city-content .city-header h3 {
                  padding: 16px 0;
                  font-size: 13px;
}
.home .main .content .container .search .city .city-content .city-header .city-breadcrumb {
                  font-size: 14px;
                  float: right;
                  padding: 16px 50px;
}
.home .main .content .container .search .city .city-content .city-header .city-breadcrumb .highlight {
                    color: #0089dc;
}
.home .main .content .container .search .city .city-content .city-guess {
                padding: 17px 0;
                border-bottom: 1px solid #eee;
}
.home .main .content .container .search .city .city-content .city-guess .city-quickguess {
                  display: inline-block;
                  font-size: 14px;
                  line-height: 25px;
                  padding-right: 28px;
}
.home .main .content .container .search .city .city-content .city-guess .city-quickguess .highlight {
                    color: #0089dc;
}
.home .main .content .container .search .city .city-content .city-guess .city-quickguess button {
                    margin: 0 0 0 14px;
                    padding: 2px 16px;
                    background: #ff6000;
                    border: none;
                    border-radius: 3px;
                    font-size: 16px;
                    color: #fff;
                    cursor: pointer;
}
.home .main .content .container .search .city .city-content .city-guess .city-search {
                  display: inline-block;
                  height: 30px;
                  border-left: 1px solid #ddd;
                  padding-left: 28px;
                  position: relative;
}
.home .main .content .container .search .city .city-content .city-guess .city-search input {
                    font-size: 12px;
                    background-color: #f5f5f5;
                    border: 1px solid #ededed;
                    width: 200px;
                    line-height: 28px;
                    padding: 0 12px 0 15px;
                    outline: 0;
}
.home .main .content .container .search .city .city-content .city-guess .city-search ul {
                    position: absolute;
                    top: 30px;
                    left: 28px;
                    border: 1px solid #eee;
                    border-top: 0;
                    background-color: white;
                    z-index: 100;
}
.home .main .content .container .search .city .city-content .city-guess .city-search ul li {
                      width: 227px;
                      clear: both;
                      height: 28px;
                      padding: 0 12px;
                      cursor: pointer;
                      box-sizing: border-box;
                      overflow: hidden;
}
.home .main .content .container .search .city .city-content .city-guess .city-search ul li span {
                        line-height: 28px;
                        font-size: 12px;
}
.home .main .content .container .search .city .city-content .city-guess .city-search ul li span:first-child {
                        float: right;
}
.home .main .content .container .search .city .city-content .city-guess .city-search ul li:hover {
                        background-color: #f2f5f7;
}
.home .main .content .container .search .city .city-content .city-list {
                position: relative;
                font-size: 14px;
}
.home .main .content .container .search .city .city-content .city-list dl {
                  padding-left: 45px;
}
.home .main .content .container .search .city .city-content .city-list dl dt {
                    position: absolute;
                    left: 0;
                    line-height: 42px;
}
.home .main .content .container .search .city .city-content .city-list dl dd {
                    display: inline-block;
                    width: 77px;
                    text-align: left;
                    line-height: 28px;
                    white-space: nowrap;
                    text-overflow: ellipsis;
                    overflow: hidden;
}
.home .main .content .container .search .search-navbar {
            width: 70%;
            float: left;
            height: 100%;
}
.home .main .content .container .search .search-navbar form {
              height: 100%;
              position: relative;
}
.home .main .content .container .search .search-navbar form:before {
                content: "";
                position: absolute;
                top: 13px;
                height: 30px;
                border-left: 1px solid #dcdcdc;
}
.home .main .content .container .search .search-navbar form input {
                height: 100%;
                display: block;
                width: 100%;
                padding-left: 18px;
                border: none;
                outline: 0;
}
.home .main .content .container .search .search-navbar form button {
                position: absolute;
                right: -166px;
                top: 0;
                width: 128px;
                line-height: 56px;
                font-size: 20px;
                padding: 0;
                background-color: #f74342;
                border: none;
                color: white;
}
.home .main .content .container .search .search-navbar form button:hover {
                  background-color: #e62625;
}
.home .footer {
    width: 1180px;
    margin: 0 auto;
    background-color: #fff;
    text-align: center;
    padding-top: 60px;
}
.home .footer .footer-app {
      display: inline-block;
      margin-bottom: 60px;
}
.home .footer .footer-app .footer-img {
        display: inline-block;
        width: 160px;
        padding: 15px;
        border: 1px solid #ddd;
        position: relative;
        margin-right: 30px;
}
.home .footer .footer-app .footer-img img {
          display: block;
          width: 100%;
          height: 100%;
}
.home .footer .footer-app .footer-img span {
          position: absolute;
          bottom: 0;
          margin-bottom: -10px;
          left: 25px;
          right: 25px;
          background-color: #fff;
          text-align: center;
          color: #999;
}
.home .footer .footer-app .footer-text {
        display: inline-block;
        line-height: 30px;
        padding: 30px 0;
        vertical-align: top;
        text-align: left;
}
.home .footer .footer-app .footer-text strong {
          color: red;
          font-size: 30px;
          font-weight: bold;
}
.home .footer .mapfooter-link {
      color: #666;
      font-size: 12px;
      line-height: 1;
      margin-bottom: 10px;
}



