
.focus {
  color: white;
  color: #fff;
  background-color: #0089dc;
  border-radius: 3px;
}
.active {
  background-color: #f6f6f6;
  border-radius: 0;
  margin: 0;
  padding: 0 16px;
  height: 36px;
}
.store-container {
  padding: 10px 10px 10px 98px;
  position: relative;
  overflow: hidden;
  background-color: white;
  border: 1px solid #e6e6e6;
  text-align: center;
}
.store-container .excavator-filter {
    overflow: hidden;
}
.store-container .excavator-filter li {
      padding: 0 10px;
      margin: 5px 6px;
      float: left;
      line-height: 36px;
      color: #666;
}
.store-container .excavator-filter li:hover {
        background-color: #f6f6f6;
        border-radius: 3px;
}
.store-container .excavator-filter-subbox {
    background-color: #f6f6f6;
    padding: 6px 0;
    overflow: hidden;
}
.store-container .excavator-filter-subbox li {
      padding: 0 10px;
      margin: 5px 6px;
      float: left;
      line-height: 26px;
}
.store-container .excavator-filter-name {
    position: absolute;
    top: 20px;
    left: 20px;
    color: #666;
}



