
.store-block {
  margin-top: 20px;
}
.store-block .place-rstbox {
    border: 1px solid #e6e6e6;
    overflow: hidden;
}
.store-block .place-rstbox ul li {
      width: 25%;
      float: left;
      height: 140px;
      border-bottom: 1px #f5f5f5 solid;
      background-color: white;
}
.store-block .place-rstbox ul li:hover {
        background-color: #f5f5f5;
}
.store-block .place-rstbox ul li .rstblock-logo {
        padding: 20px;
        color: #999;
        text-align: center;
        float: left;
}
.store-block .place-rstbox ul li .rstblock-logo .rstblock-logo-icon {
          display: block;
          width: 70px;
          height: 70px;
          margin-bottom: 10px;
}
.store-block .place-rstbox ul li .rstblock-content {
        padding: 20px;
        font-size: 12px;
}
.store-block .place-rstbox ul li .rstblock-content .rstblock-title {
          font-size: 16px;
          margin-bottom: 6px;
          font-weight: 600;
          overflow: hidden;
          white-space: nowrap;
          text-overflow: ellipsis;
          color: #333;
}
.store-block .place-rstbox ul li .rstblock-content div i {
          display: inline-block;
          vertical-align: middle;
          color: #fff;
          font-style: normal;
          font-size: 12px;
          line-height: 16px;
          overflow: hidden;
          text-align: center;
          width: 18px;
          border-radius: 2px;
          margin-right: 3px;
          padding: 1px;
          white-space: nowrap;
}



